.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--background-modal-overlay);
    z-index: var(--layer-3);
    animation: fadeIn 0.267s ease forwards;
    .modal-content {
        position: relative;
        margin: 8px;
        padding: 12px 24px 24px;
        background: var(--background-modal);
        border-radius: var(--round-medium);
        min-width: 480px;
        min-height: 120px;
        max-height: 90vh;
        opacity: 0;
        animation: fadeSlideDown 0.267s ease forwards;
        animation-delay: 0.267s;
    }
}
@media only screen and (max-width: 800px) {
    .modal-container {
        align-items: flex-start;
        .modal-content {
            min-width: 90%;
            min-height: 240px;
            overflow-y: auto;
            overflow-x: hidden;
        }
    }
}